.gallery {
  display: flex;
  flex-wrap: wrap;
  max-width: 1920px;
  margin: 0 auto;
}

.gallery_img_block {
  text-align: center;
  width: 25%;
  cursor: pointer;
  position: relative;
  &:hover {
    .filter_block {
      opacity: .8;
    }
    .filter_c {
      opacity: 1;
    }
  }
}

.filter_block {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgb(91, 108, 235);
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity .3s ease;
}

.filter_c {
  position: absolute;
  width: 57px;
  height: 57px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 1);
  z-index: 3;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity .3s ease;
}

@media (max-width: 800px) {
  .gallery_img_block {
    width: 50%;
  }
}

@media (max-width: 600px) {
  .gallery_img_block {
    width: 100%;
  }
}