.block1 {
  background-image: url(../img/index/slide_bgr.jpg);
  background-position: center;
  background-size: cover;
}

.block_form {
  max-width: 1920px;
  margin: 0 auto;
  padding: 100px 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrap-form {
  border-radius: 20px;
  box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  padding: 35px 30px;
  margin-left: 180px;
  &__title {
    color: $color_main;
    font-weight: 600;
    font-size: 26px;
    padding-bottom: 20px;
  }

}

.form {
  display: flex;
  flex-direction: column;
  &__name,
  &__phone {
    border: 0;
    border-radius: 8px;
    background-color: #f1f1f1;
    width: 230px;
    height: 50px;
    margin-bottom: 17px;
    padding-left: 10px;
    outline-color: $color_main;
  }
  &__submit {
    border: 1px solid $color_main;
    background-color: $color_main;
    border-radius: 8px;
    color: #fff;
    font-weight: 700;
    font-size: 16px;
    align-self: flex-end;
    padding: 12px 64px;
    margin-top: 31px;
    cursor: pointer;
    &:hover {
      background-color: #5b61d6;
    }
  }
}

.slogan {
  padding-left: 90px;
  color: #fff;
  &__title {
    font-size: 48px;
    text-transform: uppercase;
    padding-bottom: 20px;
  }
  &__text {
    font-family: freestylescript, sans-serif;
    font-size: 40px;
    line-height: 1.3;
  }
}

/*select*/
.select {
  position: relative;
}
.select select {
  display: block;
  width: 100%; /* от ширины блока div */
  background: #f1f1f1; /* убираем фон */
  border: none;
  border-radius: 8px;
  -webkit-appearance: none; /* Chrome */
  -moz-appearance: none; /* Firefox */
  appearance: none; /* убираем дефолнтные стрелочки */
  font-family: inherit; /* наследует от родителя */
  height: 50px;
  width: 230px;
  padding-left: 10px;
  color: #555;
  outline-color: $color_main;
  &::-ms-expand {
    display: none;
  }
}
.select:after {
    content: "";
    display: block;
    border-style: solid;
    border-width: 6px 5px 0 5px;
    border-color: #636363 transparent transparent transparent;
    pointer-events: none;
    position: absolute;
    top: 50%;
    right: 16px;
    z-index: 1;
    margin-top: -3px;
}
/*select*/

@media (max-width: 1100px) {
  .wrap-form {
    margin-left: 0;
  }
}

@media (max-width: 900px) {
  .slogan {
    padding-left: 32px;
    &__title {
      font-size: 36px;
    }

    &__text {
      font-size: 32px;
    }
    
  }
}

@media (max-width: 640px) {
  .block_form {
    flex-direction: column;
    padding: 0 15px;
    background-size: cover;
  }

  .wrap-form {
    order: 1;
    margin: 32px 0;
  }

  .slogan {
    padding-left: 0;
    padding-top: 32px;
    text-align: center;
    &__title {
      font-size: 32px;
    }
  }
}