/*variables*/
$fonts: "Open Sans", sans-serif;
$color_main: #5b6ceb;
$color_fonts: #555;
/*variables*/

/*reset*/
*,
*::before,
*::after {
  box-sizing: border-box;
}


html {
  line-height: 1.3;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  min-height: 100vh;
  margin: 0;
  font-family: $fonts;
  font-size: 14px;
  line-height: 1.5;
  color: $color_fonts;
}

a {
  text-decoration: none;
  color: $color_fonts;
}

img {
  max-width: 100%;
  vertical-align: middle;
  border-style: none;
}

h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul[class],
ol[class] {
  padding: 0;
  list-style: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  border: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

[hidden] {
  display: none !important;
}
/*reset*/

/*sprite png*/
.icon {
  display: inline-block;
}
/*sprite png*/
main {
  display: block;
}

