.select:after {
    content: "";
    display: block;
    border-style: solid;
    border-width: 6px 5px 0 5px;
    border-color: #636363 transparent transparent transparent;
    pointer-events: none;
    position: absolute;
    top: 50%;
    right: 16px;
    z-index: 1;
    margin-top: -3px;
}
