.info {
  background-image: url(../img/index/info_bg.jpg);
  background-position: center;
  background-size: cover;
}

.info-wrap {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 110px 0 90px;
}

.info-block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px;
  width: 25%;
  color: #fff;
  text-align: center;
  margin-bottom: 25px;
  &__icon {
    width: 70px;
    height: 70px;
    border: 2px solid #fff;
    border-radius: 50%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 27px;
  }
  &__title {
    font-weight: 700;
    font-size: 18px;
    text-transform: uppercase;
    padding-bottom: 14px;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
  }
  &__text {
    font-size: 16px;
    line-height: 1.7;
    margin-top: auto;
  }
}

@media (max-width: 1180px) {
  .info-block__title {
    width: 100%;
  }
}

@media (max-width: 1000px) {
  .info-block {
    width: 33.333333%;
  }

  .info-wrap {
    padding: 52px 0 40px;
  }
}

@media (max-width: 800px) {
  .info-block {
    width: 50%;
  }
}

@media (max-width: 500px) {
  .info-block {
    width: 100%;
  }

  .info-wrap {
    padding: 32px 0 24px;
  }
}