.footer {
  padding-top: 100px;
  color: #fff;
  background-color: $color_main;
  a {
    color: #fff;
  }
}

.f-wrap {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.f-copyright {
  text-align: center;
  background-color: #5363d8;
  color: #b7bffc;
  padding: 41px 0;
  margin-top: 100px;
}

.f-about,
.f-contact {
  width: 50%;
  padding: 0 15px;
  &__title {
    text-transform: uppercase;
    font-size: 18px;
    margin-bottom: 52px;
  }
}

.f-about {
  &__text {
    padding-right: 100px;
    padding-bottom: 70px;
  }
}

.f-about__icons {
  a + a {
    padding-left: 12px;
  }
}

.f-contact {
  &__wrap {
    display: flex;
    flex-wrap: wrap;
  }
  &__addr {
    position: relative;
    padding-left: 36px;
    width: 50%;
    padding-bottom: 52px;
    .icon {
      position: absolute;
      left: 0;
      top: 4px;
    }
    .icon-addr_site {
      top: 2px;
    }
  }
  &__phone {
    display: block;
  }
}

@media (max-width: 900px) {
  .footer {
    padding-top: 52px;
  }
  .f-copyright {
    margin-top: 52px;
  }
  .f-contact {
    &__addr {
      width: 100%;
      padding-bottom: 24px;
    }
  }

  .f-about,
  .f-contact {
    &__title {
      margin-bottom: 32px;
    }
  }

  .f-about {
    &__text {
      padding-right: 60px;
      padding-bottom: 40px;
    }
  }
}

@media (max-width: 650px) {
  .f-wrap {
    flex-wrap: wrap;
  }
  .f-about {
    &__text {
      padding-right: 0;
      padding-bottom: 32px;
    }
  }

  .f-about,
  .f-contact {
    width: 100%;
    &__icons {
      margin-bottom: 52px;
    }
  }

  .f-copyright {
    margin-top: 32px;
    padding: 16px 0;
  }

}