.programs {
  padding: 125px 0;
}

.prog-title {
  font-size: 30px;
  text-transform: uppercase;
  color: $color_main;
  text-align: center;
}

.prog-title2 {
  font-family: freestylescript, sans-serif;
  font-size: 41px;
  text-align: center;
  font-weight: 400;
  padding-bottom: 42px;
}

.prog-wrap {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  &__block {
    padding: 15px 15px 10px;
    width: 50%;
  }
}

.prog-block {
  border-radius: 8px;
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.1);
  display: flex;
  &__img {
    flex-shrink: 0;
  }
  &__text-block {
    padding: 42px 30px 0;
  }
  &__title {
    font-size: 20px;
    text-transform: uppercase;
    color: #777;
    padding-bottom: 22px;
  }
  &__text2 {
    color: #999;
    padding-top: 11px;
  }
}

@media (max-width: 1200px) {
  .prog-block {
    &__img {
      width: 40%;
    }
    &__text-block {
      padding: 16px 20px 0;
    }
    &__title {
      padding-bottom: 8px;
    }
  }
}

@media (max-width: 1000px) {
  .programs {
    padding: 70px 0;
  }

  .prog-wrap {
    &__block {
      padding: 15px 15px 10px;
      width: 100%;
    }
  }

  .prog-block {
    &__img {
      width: auto;
    }
    &__text-block {
      padding: 16px 20px 0;
    }
    &__title {
      padding-bottom: 8px;
    }
  }

  .prog-title2 {
    padding-bottom: 32px;
  }
}

@media (max-width: 600px) {
  .programs {
    padding: 32px 0;
  }

  .prog-block {
    &__img {
      width: 40%;
    }
  }

  .prog-title2 {
    padding-bottom: 12px;
  }
}

@media (max-width: 500px) {
  .prog-block {
    flex-direction: column;
    padding-top: 16px;
    &__img {
      width: auto;
      text-align: center;
      img, 
      picture {
        border-radius: 8px;
      }
    }
    &__text-block {
      padding-bottom: 16px;
    }
  }

  .prog-title {
    font-size: 26px;
  }
}