.block6 {
  background-image: url(../img/index/price-bg.png);
  background-position: bottom right;
  background-repeat: no-repeat;
  padding: 85px 0 200px;
}

.price {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
}

.price-wrap {
  padding: 15px;
  width: 33.333333%;
  margin-bottom: 105px;
}

.price-block {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0px 9px 30px 0px rgba(0, 0, 0, 0.1);
  text-align: center;
  padding-bottom: 52px;
  &__price {
    background-color: $color_main;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 67px;
    line-height: .8;
    font-weight: 700;
    padding: 48px 0;
    margin-bottom: 42px;
    span {
      font-size: 18px;
      text-transform: uppercase;
      align-self: flex-end;
      margin-left: 4px;
    }
  }
  &__button {
    background-color: $color_main;
    color: #fff;
    border-radius: 20px;
    font-size: 18px;
    font-weight: 600;
    padding: 10px 48px 13px;
    order: 1;
    align-self: center;
    margin-top: 42px;
    &:hover {
      background-color: #5b61d6;
      & + .price-block__price {
        background-color: #5b61d6;
      }
    }
  }
  .icon {
    margin-right: 20px;
  }
  &__list {
    position: relative;
    font-size: 18px;
    text-transform: uppercase;
    & + *:before {
      content: "";
      height: 1px;
      width: 100px;
      background-color: #d6d6d6;
      display: block;
      margin: 16px auto;
    }
  }
}

.reviews {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;
}

.swiper-slide_rev {
  background-color: #fff;
  padding: 32px 200px 64px 60px;
  display: flex;
  align-items: center;
}

.slider2 {
  width: 100%;
  height: auto;
  box-shadow: 0px 9px 30px 0px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  background-color: #fff;
}

.slide2_navs {
  position: absolute;
  bottom: -35px;
  left: 278px;
  z-index: 100;
  display: flex;
  align-items: center;
}

.rev_img_nav {
  width: 72px;
  height: 72px;
  border-radius: 50%;
  border: 2px solid #d6d6d6;
  padding: 2px;
  overflow: hidden;
  margin-right: 12px;
  text-align: center;
  background-color: #fff;
}

.rev_img_name_block {
  display: flex;
  align-items: center;
  padding: 0 30px;
}

.slider2-prev,
.slider2-next {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  box-shadow: 0px 5px 9px 0px rgba(0, 0, 0, 0.2);
  z-index: 100;
  background-color: #fff;
  cursor: pointer;
  &:before {
    position: absolute;
    content: '';
    width: 9px;
    height: 9px;
    border-bottom: 2px solid $color_main;
    border-right: 2px solid $color_main;
    z-index: 101;
  }
}

.slider2-prev {
  &:before {
    transform: rotate(135deg);
    position: relative;
    left: 2px;
  } 
}

.slider2-next {
  &:before {
    transform: rotate(-45deg);
    position: relative;
    right: 1px;
  } 
}

.rev_name1 {
  font-weight: 700;
}

.rev_name2 {
  font-size: 12px;
}

.slide_img_block {
  width: 200px;
  flex-shrink: 0;
  position: relative;
  img {
    width: 148px;
    height: 148px;
    border: 2px solid #d6d6d6;
    border-radius: 50%;
    padding: 10px;
  }
  &:after {
    position: absolute;
    content: "";
    width: 2px;
    height: 75px;
    background-color: #d6d6d6;
    display: block;
    right: 30px;
    bottom: 12px;
  }
}

.rev_text {
  font-style: italic;
  padding-bottom: 27px;
  position: relative;
  .icon {
    position: absolute;
  }
  .icon-br_open {
    left: -40px;
    top: 5px;
  }
  .icon-br_close {
    right: -20px;
    bottom: 0;
  }
}

.rev_work {
  color: #777;
}

.rev_name {
  font-size: 16px;
  color: $color_main;
  font-weight: 700;
}

.swiper-pagination {
  right: -30px;
  top: 50%;
  transform: translate(0,-50%);
  display: flex;
  flex-direction: column;
}

.swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  padding: 2px;
  border: 2px solid $color_main;
  background: transparent;
  position: relative;
  &+* {
    margin-top: 18px;
  }
  &-active {
    &::after {
      content: '';
      position: absolute;
      height: 10px;
      width: 10px;
      border-radius: 50%;
      background-color: $color_main;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
  }
  
}

@media (max-width: 1320px) {
  .swiper-pagination {
    right: 40px;
  }
}

@media (max-width: 1000px) {
  .swiper-slide_rev {
    padding: 32px 100px 32px 40px;
  }
  .block6 {
    padding: 52px 0 120px;
  }
}

@media (max-width: 900px) {
  .price-wrap {
    width: 50%;
    margin-bottom: 0;
  }
}

@media (max-width: 700px) {
  .swiper-slide_rev {
    flex-direction: column;
    padding-left: 32px;
    padding-right: 32px;
  }

  .slide_img_block {
    width: auto;
    flex-shrink: 1;
    position: relative;
    margin-bottom: 12px;
    img {
      width: 148px;
      height: 148px;
      border: 2px solid #d6d6d6;
      border-radius: 50%;
      padding: 10px;
    }
    &:after {
      display: none;
    }
  }

  .rev_text {
    .icon-br_open {
      left: 0;
      top: -40px;
    }

    .icon-br_close {
      right: 0;
    }
  }

  .slide2_navs {
    position: absolute;
    bottom: -35px;
    left: 50%;
    transform: translate(-50%);
    z-index: 100;
    display: flex;
    align-items: center;
  }

  .rev_img_name_block {
    padding: 0 12px;
  }

  .swiper-pagination {
    display: none;
  }

  .block6 {
    padding: 32px 0 64px;
  }
  
}

@media (max-width: 600px) {
  .price-wrap {
    width: 100%;
  }

  .slide2_navs {
    left: 15%;
    transform: none;
  }

}