.header {
  position: fixed;
  width: 100%;
  background-color: #fff;
  z-index: 1000;
}
.main {
  padding-top: 100px;
}
.logo-block {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  &__name {
    font-family: "ChampagneLimousines";
    font-size: 24px;
    color: $color_main;
    letter-spacing: 2.2px;
    text-transform: uppercase;
    line-height: 1;
  }
}

.burger {
  display: none;
}

.menu {
  z-index: 10000;
  max-width: 880px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  &__item {
    position: relative;
    font-weight: 600;
    font-size: 14px;
    margin: 40px 0 39px;
    text-align: center;
    flex-basis: 82px;
    & + & {
      margin-left: 20px;
    }
    &:nth-child(3) {
      margin-right: auto;
    }
    &:hover {
      color: $color_main;
      &:after {
        position: absolute;
        content: '';
        height: 1px;
        width: 30px;
        background-color: $color_main;
        bottom: -2px;
        left: 50%;
        transform: translate(-50%);
      }
    }
  }
}

.anchor {
  &::before {
    display: block;
    content: "";
    height: 100px;
    margin: -100px 0 0;
  }
}

@media (max-width: 700px) {
  .logo-block {
    position: static;
    transform: none;
    padding-left: 15px;
    &__name {
      padding-left: 10px;
    }
  }

  .header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  .menu {
    justify-content: flex-end;
    margin: 0;
    padding-right: 15px;
    &__item {
      flex-basis: auto;
      padding: 36px 0;
      margin: 0;
    }
  }

  .main {
    padding-top: 90px;
  }
}

@media (max-width: 550px) {
  .menu {
    display: none;
    width: 100%;
    height: 90vh;
    background-color: #fff;
    padding-top: 12px;
    &__item {
      display: block;
      font-size: 20px;
      padding: 16px 0;
      margin: 0;
      & + & {
        margin-left: 0;
      }
    }
  }

  .menu_open {
    display: block;
  }

  .burger {
    display: block;
    margin-right: 15px;
    outline: none;
    width: 26px;
    height: 21px;
    background: linear-gradient(to bottom, $color_main 3px, transparent 3px, transparent 9px, $color_main 9px, $color_main 12px, transparent 12px, transparent 18px, $color_main 18px);
    &_active {
      background: linear-gradient(45deg, transparent 45%, $color_main 46%, $color_main 55%, transparent 56%), linear-gradient(-45deg, transparent 45%, $color_main 46%, $color_main 55%, transparent 56%);
    }
}

  .logo-block {
    display: flex;
    align-items: center;
    padding: 10px 15px;
  }

  .main {
    padding-top: 62px;
  }
}