.about {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  padding: 125px 0;
}

.about-block {
  position: relative;
  width: 50%;
  padding: 15px;
  &__title {
    font-size: 30px;
    text-transform: uppercase;
    color: $color_main;
    padding-bottom: 14px;
  }
  &__text {
    padding-right: 70px;
  }
  &_mod {
    flex-shrink: 0;
  }
}

.slider1 {
  max-width: 554px;
  height: auto;
  .swiper-slide {
    border-radius: 8px;
    overflow: hidden;
  }
}

.slider1-prev,
.slider1-next {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  box-shadow: 0px 9px 9px 0px rgba(0, 0, 0, 0.2);
  z-index: 100;
  position: absolute;
  top: 40%;
  background-color: #fff;
  cursor: pointer;
  margin-top: calc(-1 * var(--swiper-navigation-size)/ 2);
  &:before {
    position: absolute;
    content: '';
    width: 14px;
    height: 14px;
    border-bottom: 3px solid $color_main;
    border-right: 3px solid $color_main;
  }
}

.slider1-prev {
  left: -2px;
  right: auto;
  &:before {
    transform: rotate(135deg);
    left: 20px;
    bottom: 15px;
  } 
}

.slider1-next {
  right: -2px;
  left: auto;
  &:before {
    transform: rotate(-45deg);
    right: 20px;
    bottom: 15px;
  } 
}

@media (max-width: 1250px) {
  .about-block {
    width: 40%;
    &_mod {
      width: 60%;
    }
    &__text {
      padding-right: 32px;
    }
  }

  .slider1-prev {
    left: 58px;
  }
  
  .slider1-next {
    right: 58px;
  }
}

@media (max-width: 1000px) {
  .about {
    flex-direction: column;
    padding: 52px 0;
  }
  .about-block {
    width: 100%;
    &__title {
      text-align: center;
    }
  }
}

@media (max-width: 730px) {
  .slider1-prev {
    left: 10px;
  }
  
  .slider1-next {
    right: 10px;
  }
}

@media (max-width: 500px) {
  .about {
    flex-direction: column;
    padding: 20px 0;
  }

  .about-block {
    &__title {
      font-size: 26px;
    }
  }

  .slider1-prev,
  .slider1-next {
    width: 40px;
    height: 40px;
    top: 48%;
    &:before {
      width: 12px;
      height: 12px;
      border-bottom: 3px solid $color_main;
      border-right: 3px solid $color_main;
    }
  }

  .slider1-prev {
    left: 20px;
    right: auto;
    &:before {
      transform: rotate(135deg);
      left: 16px;
    } 
  }
  
  .slider1-next {
    right: 20px;
    left: auto;
    &:before {
      transform: rotate(-45deg);
      right: 16px;
    } 
  }
}